import { FC, useEffect, useState } from "react";

// components imports
import Permission from "../Permission";

// arrays imports
import { FB_IG, TW, LI } from "../data";

// interfaces imports
import { IPermissionsProps } from "./interfaces";
import { IPermission } from "../data";

import { useToastMessage } from "../../../../../contexts/ToastMessageContext";

import { getFacebookPermissions } from "../../../../../api/SocialMediaSetup";

const Permissions: FC<IPermissionsProps> = ({
  platform,
}: IPermissionsProps): JSX.Element => {
  // contexts
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  const [fbPermissions, setFbPermissions] = useState([]);

  const loadFacebookPermissions = async () => {
    const res = await getFacebookPermissions();
    try {
      if (res.success) {
        setFbPermissions(res.payload);
      }
    } catch (err) {
      setErrorMessage(err.message);
    }
  };

  useEffect(() => {
    if (platform === "facebook" || platform === "instagram") {
      loadFacebookPermissions();
    }
  }, [platform]);
  return (
    <>
      {(platform === "facebook and instagram" ||
        platform === "facebook" ||
        platform === "instagram") &&
        // FB_IG.map(({ name, desc }: IPermission, idx: number) => (
        //   <Permission name={name} desc={desc} key={idx} />
        // )
        fbPermissions.map(permission => {
          return <div>{permission}</div>;
        })}

      {platform === "twitter" &&
        TW.map(({ name, desc }: IPermission, idx: number) => (
          <Permission name={name} desc={desc} key={idx} />
        ))}

      {platform === "linkedin" &&
        LI.map(({ name, desc }: IPermission, idx: number) => (
          <Permission name={name} desc={desc} key={idx} />
        ))}
    </>
  );
};

export default Permissions;
