import React from "react";
import { Drawer, IconButton } from "@mui/material";

import leftArrow from "../../../../assets/icons/leftArrow.svg";

// styles
import useStyles from "../styles/muiStyles";

// component imports
import Permissions from "./Permissions";

const ViewPermissions = ({ anchor = "right", permissions, onClose }) => {
  const classes = useStyles();
  const displaySubtitle = () => {
    switch (permissions.provider) {
      case "twitter":
        return "Twitter";
      case "linkedin":
        return "LinkedIn";
      default:
        return "Facebook & Instagram";
    }
  };

  return (
    <Drawer
      anchor={anchor}
      open={permissions.show}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes["view-permissions"]}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            onClick={() => {
              onClose();
            }}
            style={{ color: "transparent", padding: 0 }}
          >
            <img alt="back" src={leftArrow} style={{ margin: "7.5px 10px" }} />
          </IconButton>
          <div className="header-titles">
            <h1 className="title">{"Permissions Granted"}</h1>
            <h3 className="subtitle">{displaySubtitle()}</h3>
          </div>
        </div>
        <div className="details">
          <Permissions platform={permissions.provider} />
        </div>
      </div>
    </Drawer>
  );
};

export default ViewPermissions;
