import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "settings-layout": {
      alignItems: "stretch",
      background:
        "radial-gradient(63.05% 113.84% at 94.71% 94.68%, #ECF5FF 4.4%, #F5F7F9 100%)",
      display: "flex",
      flexDirection: "row",
      justifyContent: "flex-start",

      "& .sidebar": {
        alignSelf: "auto",
        backgroundColor: "#ffffff",
        flex: "0 1 auto",
        height: "100vh",
        order: 0,
        width: "342px",
      },

      "& .content": {
        alignSelf: "auto",
        display: "flex",
        flex: "1 1 auto",
        height: "100vh",
        overflowY: "auto",
        overflowX: "hidden",
        order: 0,

        "& .inner": {
          display: "inline-flex",
          height: "max-content",
          margin: "0 auto",

          "&.fluid": {
            justifyContent: "center",
            width: "100%",
            padding: "0 30px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
