import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "mobile-settings": {
    overflow: "hidden",
    width: "100%",

    "& > .header": {
      alignItems: "center",
      borderBottom: "1px solid #24272c1a",
      display: "flex",
      gap: "20px",
      justifyContent: "center",
      height: "70px",
      padding: "20px",
      width: "100%",

      "& > a": {
        alignItems: "center",
        display: "flex",
        height: "100%",
      },

      "& .title": {
        color: "#24272c",
        fontWeight: 500,
        fontSize: "24px",
        lineHeight: "28px",
      },
    },

    "& .content": {
      display: "flex",
      flexDirection: "column",
      padding: "30px 30px 40px",
      rowGap: "36px",
      width: "100%",

      "& > .entry": {
        color: "#24272CB3",
        fontWeight: 400,
        fontSize: "14px",
        lineHeight: "20px",
      },

      "& .MuiAccordion-root": {
        backgroundColor: "transparent",
        boxShadow: "none",

        "&::before": {
          display: "none",
        },

        "& .MuiAccordionSummary-root": {
          borderBottom: "1px solid #24272c1f",
          minHeight: "unset",
          padding: 0,

          "& .MuiAccordionSummary-content": {
            margin: "14px 0",

            "& .MuiTypography-root": {
              color: "#434A54",
              fontSize: "20px",
              fontWeight: 500,
              lineHeight: "23px",
            },
          },
        },

        "& .MuiAccordionDetails-root": {
          padding: 0,

          "& .MuiList-root": {
            "& .MuiListItemAvatar-root": {
              display: "flex",
              justifyContent: "end",
              columnGap: "10px",
              marginTop: "4px",
            },

            "& .MuiListItem-root": {
              alignItems: "flex-start",
              marginBottom: "30px",

              "& .MuiListItemText-root": {
                marginBottom: "12px",

                "& .MuiListItemText-primary": {
                  color: "#434A54",
                  fontSize: "16px",
                  fontWeight: 500,
                  lineHeight: "19px",
                },
              },

              "& .linked-account": {
                alignItems: "center",
                display: "flex",
                justifyContent: "flex-start",
                columnGap: "12px",
                marginBottom: "8px",
                width: "100%",

                "& > figure": {
                  display: "inline-block",
                  border: "1px solid rgba(36, 39, 44, 0.1)",
                  borderRadius: "8px",
                  overflow: "hidden",
                  margin: 0,

                  "& img": {
                    display: "block",
                    position: "relative",

                    "&::before": {
                      backgroundColor: "#C4C4C4",
                      content: "''",
                      display: "block",
                      height: "100%",
                      position: "absolute",
                      width: "100%",
                    },
                  },
                },

                "& .account-name": {
                  color: "#24272C",
                  fontSize: "16px",
                  fontWeight: 500,
                  lineHeight: "19px",
                },
              },

              "& .actions": {
                display: "flex",
                justifyContent: "space-between",
                columnGap: "32px",

                "& .MuiLink-root": {
                  color: "#656F7A",
                  cursor: "pointer",
                  fontSize: "12px",
                  fontWeight: 500,
                  lineHeight: "14px",
                  textDecorationColor: "#656F7A",
                },
              },
            },
          },

          "& .card-list": {
            marginTop: "35px",

            "& .card-list-item": {
              listStyle: "none",
              marginBottom: "24px",

              "& .card-list-title": {
                color: "#434A54",
                fontSize: "16px",
                fontWeight: 500,
                lineHeight: "19px",
                marginBottom: "10px",
              },

              "& .MuiButton-root": {
                color: "#ffffff",
                columnGap: "10px",
                fontSize: "12px",
                fontWeight: 700,
                height: "48px",
                lineHeight: "14px",
                padding: "0 10px",
                textTransform: "initial",

                "& .MuiButton-label": {
                  height: "100%",
                  columnGap: "10px",
                },

                "&.btn-facebook": {
                  backgroundColor: "#4267B2",
                },

                "&.btn-twitter": {
                  backgroundColor: "#1DA1F2",
                },

                "&.btn-linkedin": {
                  backgroundColor: "#0077b5",
                },
              },
            },
          },
        },
      },

      "& .card-group": {
        "& .card-title": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "21px",
          marginBottom: "30px",
        },

        "& .flex-row": {
          display: "flex",
          flexDirection: "column-reverse!important",
          gap: "30px 35px",

          "&.min-gap": {
            flexDirection: "column!important",
            columnGap: "18px",
            rowGap: "0px",
          },

          "&.check-to-fill": {
            alignItems: "center",
            backgroundColor: "#E9F1F2",
            height: "60px",
            justifyContent: "center",
            margin: "0 -30px 30px",
            padding: "9px 0",

            "& .centered-colum": {
              padding: "0 30px",

              "& .MuiFormGroup-root": {
                "& .MuiFormControlLabel-root": {
                  marginRight: 0,

                  "& .MuiCheckbox-root": {
                    color: "#24272C80",

                    "&.Mui-checked": {
                      color: "#12B0EE",
                    },
                  },

                  "& .MuiFormControlLabel-label": {
                    color: "#24272C",
                    fontSize: "16px",
                    fontWeight: 400,
                    lineHeight: "19px",
                  },
                },
              },
            },
          },
        },

        "& .field": {
          marginBottom: "30px",

          "& .MuiFormControl-root": {
            width: "100%",
          },
        },
      },

      "& .action": {
        display: "flex",
        justifyContent: "end",

        "& .MuiButton-root": {
          "& .MuiButton-label": {
            "& .text-limiter": {
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
            },
          },
        },
      },
    },
  },

  "view-permissions": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& > .header": {
        alignItems: "center",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "20px 20px",
        width: "100%",

        "& .MuiIconButton-root": {
          backgroundColor: "transparent",
        },

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 700,
          fontSize: "24px",
          lineHeight: "28px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },

        "& .subtitle": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 700,
          fontSize: "18px",
          lineHeight: "21px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& .details": {
        flex: "1 0 0",
        overflowY: "auto",
        padding: "10px 30px",

        "& h5": {
          color: "#000000",
          fontSize: "14px",
          fontWeight: 700,
          lineHeight: "16px",
        },

        "& p": {
          color: "#000000",
          fontSize: "14px",
          fontWeight: 400,
          lineHeight: "16px",
          marginBottom: "16px",
        },
      },
    },
  },

  "social-linking": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& > .header": {
        alignItems: "center",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "20px 20px",
        width: "100%",

        "& .MuiIconButton-root": {
          backgroundColor: "transparent",
        },

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "32px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& .content": {
        flex: "1 0 0",
        overflowY: "auto",
        padding: "10px 30px",

        "& .MuiList-root": {
          "& .another-connection": {
            textAlign: "center",

            "& .MuiLink-root": {
              color: "#434A54",
              fontSize: "14px",
              fontWeight: 400,
              lineHeight: "16px",
              margin: "auto",
            },
          },

          "& .my-profile": {
            "& .MuiFormControlLabel-root": {
              "& .MuiFormControlLabel-label": {
                alignItems: "baseline",
                color: "#2E3239",
                display: "flex",
                fontSize: "13px",
                fontWeight: 400,
                gap: "4px",
                lineHeight: "15px",

                "& .MuiLink-root": {
                  color: "#2E3239",
                  fontSize: "13px",
                  fontWeight: 400,
                  lineHeight: "15px",
                },
              },
            },
          },

          "& .MuiListItem-root": {
            "& .MuiListItemButton-root": {
              padding: "8px 16px",

              "& .MuiListItemAvatar-root": {
                display: "flex",

                "& .avatar": {
                  borderRadius: "8px",
                  overflow: "hidden",
                },
              },

              "& .MuiListItemText-primary": {
                color: "#24272C",
                fontSize: "16px",
                fontWeight: 500,
                lineHeight: "19px",
              },

              "& .MuiListItemText-secondary": {
                color: "#F7B500",
                fontSize: "12px",
                fontWeight: 400,
                lineHeight: "14px",
              },

              "& .MuiListItemIcon-root": {
                minWidth: "unset",
              },
            },
          },
        },

        "& .actions": {
          textAlign: "center",
          display: "table",
          margin: "0 auto",

          "& .skip": {
            alignItems: "center",
            color: "#24272C",
            cursor: "pointer",
            display: "inline-flex",
            fontSize: "13px",
            fontWeight: 400,
            lineHeight: "15px",
            opacity: 0.7,
            textDecoration: "underline",

            "&:disabled": {
              cursor: "not-allowed",
            },

            "& .label": {
              marginRight: "6px",
            },
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "stacked-cards-container": {
      display: "flex",
      flexDirection: "column",
      paddingBottom: "50px",
      paddingTop: "50px",
      maxWidth: "650px",
      rowGap: "36px",
      width: "100%",

      "& .card-group": {
        backgroundColor: "#ffffff",
        borderRadius: "10px",
        boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",
        padding: "30px 36px 40px",

        "& .card-title": {
          color: "#434A54",
          borderBottom: "1px solid #24272C1f",
          fontSize: "20px",
          fontWeight: 500,
          lineHeight: "23px",
          paddingBottom: "16px",
          marginBottom: "16px",
        },

        "& .MuiList-root": {
          "& .MuiListItemAvatar-root": {
            display: "flex",
            justifyContent: "end",
            columnGap: "10px",
            marginTop: "4px",
          },

          "& .MuiListItem-root": {
            alignItems: "flex-start",
            marginBottom: "30px",

            "& .MuiListItemText-root": {
              marginBottom: "12px",

              "& .MuiListItemText-primary": {
                color: "#434A54",
                fontSize: "16px",
                fontWeight: 500,
                lineHeight: "19px",
              },
            },

            "& .linked-account": {
              alignItems: "center",
              display: "flex",
              justifyContent: "flex-start",
              columnGap: "12px",
              marginBottom: "8px",
              width: "100%",

              "& > figure": {
                display: "inline-block",
                border: "1px solid rgba(36, 39, 44, 0.1)",
                borderRadius: "8px",
                overflow: "hidden",
                margin: 0,

                "& img": {
                  display: "block",
                  position: "relative",

                  "&::before": {
                    backgroundColor: "#C4C4C4",
                    content: "''",
                    display: "block",
                    height: "100%",
                    position: "absolute",
                    width: "100%",
                  },
                },
              },

              "& .account-name": {
                color: "#24272C",
                fontSize: "16px",
                fontWeight: 500,
                lineHeight: "19px",
              },
            },

            "& .actions": {
              display: "flex",
              justifyContent: "space-between",
              columnGap: "32px",

              "& .MuiLink-root": {
                color: "#656F7A",
                cursor: "pointer",
                fontSize: "12px",
                fontWeight: 500,
                lineHeight: "14px",
                textDecorationColor: "#656F7A",
              },
            },
          },
        },

        "& .card-list": {
          marginTop: "35px",

          "& .card-list-item": {
            listStyle: "none",
            marginBottom: "24px",

            "& .card-list-title": {
              color: "#434A54",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "19px",
              marginBottom: "10px",
            },

            "& .MuiButton-root": {
              color: "#ffffff",
              columnGap: "10px",
              fontSize: "12px",
              fontWeight: 700,
              height: "48px",
              lineHeight: "14px",
              padding: "0 10px",
              textTransform: "initial",

              "& .MuiButton-label": {
                height: "100%",
                columnGap: "10px",
              },

              "&.btn-facebook": {
                backgroundColor: "#4267B2",
              },

              "&.btn-twitter": {
                backgroundColor: "#1DA1F2",
              },

              "&.btn-linkedin": {
                backgroundColor: "#0077b5",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    "stacked-cards-container": {
      "& .card-group": {},
    },
  },
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
