import React from "react";

import BusinessSettingSideBar from "../../../Container/BusinessSettings/SideBar";

import useStyles from "./styles/muiStyles";

const Layout = ({ useFluid = false, children }) => {
  const classes = useStyles();

  return (
    <div className={classes["settings-layout"]}>
      <div className="sidebar">
        <BusinessSettingSideBar />
      </div>
      <div className="content">
        <div className={`inner${useFluid ? " fluid" : ""}`}>{children}</div>
      </div>
    </div>
  );
};

export default Layout;
