/* eslint-disable react-hooks/exhaustive-deps */
import { FunctionComponent, useEffect, useRef, useState } from "react";
import {
  Divider,
  FormControl,
  Link,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  MenuItem,
  OutlinedInput,
  Select,
} from "@mui/material/";
import { Collapse, withStyles } from "@material-ui/core";

import { useBusinessProfile } from "contexts/BusinessProfileContext";
import { useBusiness } from "contexts/BusinessProvider";
import { useSidebar } from "contexts/SidebarProvider/SidebarProvider";
import Brandlogo from "../Brandlogo/Brandlogo";

import useStyles from "./styles/muiStyles";
import GearIcon from "./icons/gear";
import PeoplesIcon from "./icons/peoples";
import LeadIcon from "./icons/lead";
import WhatsAppIcon from "./icons/whatsapp";
import SocialIcon from "./icons/social";
import ExpandMore from "./icons/expand-more";
import ExpandLess from "./icons/expand-less";

interface ISettingSideBarProps {}

const StyledListItem = withStyles({
  root: {
    borderRadius: "12px",
    cursor: "pointer",
    marginBottom: "6px",
    width: "100%",
    height: "42px",

    "&:hover": {
      backgroundColor: "rgba(159, 159, 159, 0.1)!important",

      "& svg": {
        "& path": {
          fill: "#464A51",
        },
      },

      "& .MuiListItemText-primary": {
        color: "#464A51",
      },
    },

    "& .MuiListItem-root": {
      paddingLeft: "32px",
    },

    "&.Mui-selected": {
      backgroundColor: "#32c5ff1a!important",

      "& svg": {
        "& path": {
          fill: "#464A51",
        },
      },

      "& .MuiListItemText-primary": {
        color: "#464A51",
      },
    },

    "& .MuiListItemIcon-root": {
      minWidth: "30px",
    },

    "& svg": {
      "& path": {
        fill: "#686C72",
      },
    },

    "& .MuiListItemText-primary": {
      color: "#686C72",
      fontSize: "15px!important",
      fontWeight: 500,
      lineHeight: "18px!important",
    },
  },
})(ListItem);

const StyledMenuItem = withStyles({
  root: {
    alignItems: "center",
    columnGap: "10px",
    display: "flex",
    justifyContent: "flex-start",
    padding: "6px 16px",

    "& .image, & .link": {
      display: "flex",
    },
  },
})(MenuItem);

const SideBar: FunctionComponent<ISettingSideBarProps> = (props) => {
  const classes = useStyles();
  const [open, setOpen] = useState<string | false>(false);
  // const [planCategory, setPlanCategory] = useState([]);
  const { activePlans } = useSidebar();
  const {
    getAllUserBusiness,
    fetchDefaultBusiness,
    selectedBusiness,
    allBusiness,
  } = useBusinessProfile();
  const { switchBusiness } = useBusiness();
  const planCategory = activePlans.map(({ product }) => product);
  const handleClick = (panel) => {
    setOpen((opened) => (opened === false || opened !== panel ? panel : false));
  };
  const loadDataRef = useRef(() => {
    !selectedBusiness && fetchDefaultBusiness();
    allBusiness.length < 1 && getAllUserBusiness();
  });
  const exitSetting = () => {
    const redirectTo = selectedBusiness?.redirectUrl
      ? selectedBusiness.redirectUrl
      : "/engage/chat";
    window.location.href = redirectTo;
  };
  const switchBetweenBusiness = (e) => {
    if (selectedBusiness.id === e.target.value) {
      return;
    }
    const business = allBusiness.find(
      (data) => data.businessId === e.target.value
    );
    if (business) {
      switchBusiness(e.target.value, business);
      window.location.reload();
    }
  };

  // useEffect(() => {
  //   if (selectedBusiness) {
  //     let plan = selectedBusiness.planCategory
  //       ? selectedBusiness.planCategory
  //       : [];
  //     if (planCategory.length === 0) {
  //       setPlanCategory(plan);
  //     }
  //   }
  // }, [selectedBusiness]);

  useEffect(() => {
    loadDataRef.current();
  }, []);

  return (
    <div className={classes.wrap}>
      <div className="head">
        <div className="above">
          <Brandlogo />
          <h4 className="page-title">{"BUSINESS SETTINGS"}</h4>
        </div>
        <div className="below">
          <small>{"SELECT BUSINESS"}</small>
          <div className="select-business">
            <FormControl variant="filled" fullWidth>
              <Select
                input={<OutlinedInput />}
                inputProps={{ "aria-label": "Without label" }}
                name="business"
                value={selectedBusiness?.id || ""}
                onChange={switchBetweenBusiness}
              >
                {allBusiness &&
                  allBusiness.map(({ business }, i) => (
                    <StyledMenuItem key={i} value={business.id}>
                      <span
                        className="image"
                        style={{
                          backgroundColor: "#24272C1A",
                          height: "20px",
                          width: "20px",
                        }}
                      >
                        {business.logo && business.logo !== "null" && (
                          <img
                            src={business.logo}
                            alt={`Business: ${business.business_name}`}
                            style={{ width: "20px" }}
                          />
                        )}
                      </span>
                      <span className="label">{business.business_name}</span>
                    </StyledMenuItem>
                  ))}
              </Select>
            </FormControl>
          </div>
        </div>
      </div>
      <List className="menu">
        <Link
          underline="none"
          color="inherit"
          href="/business/general-settings"
          aria-label="General Settings"
        >
          <StyledListItem
            selected={window.location.pathname.includes("general-settings")}
          >
            <ListItemIcon>
              <GearIcon />
            </ListItemIcon>
            <ListItemText primary="General Settings" />
          </StyledListItem>
        </Link>
        <Link
          underline="none"
          color="inherit"
          href="/business/team-members"
          aria-label="Team Members"
        >
          <StyledListItem
            selected={window.location.pathname.includes("team-members")}
          >
            <ListItemIcon>
              <PeoplesIcon />
            </ListItemIcon>
            <ListItemText primary="Team Members" />
          </StyledListItem>
        </Link>
        <StyledListItem
          onClick={() => handleClick("lead")}
          selected={window.location.pathname.includes("lead")}
        >
          <ListItemIcon>
            <LeadIcon />
          </ListItemIcon>
          <ListItemText primary="Lead Management" />
          {open === "lead" || window.location.pathname.includes("lead") ? (
            <ExpandLess />
          ) : (
            <ExpandMore />
          )}
        </StyledListItem>
        <Collapse
          in={open === "lead" || window.location.pathname.includes("lead")}
          timeout="auto"
          className="submenu"
          unmountOnExit
        >
          <List component="div" disablePadding>
            <Link
              underline="none"
              color="inherit"
              href="/business/lead-data-fields"
              aria-label="Lead Data Fields"
            >
              <StyledListItem
                sx={{ paddingLeft: "32px!important" }}
                selected={window.location.pathname.includes("lead-data-fields")}
              >
                <ListItemText primary="Lead Data Fields" />
              </StyledListItem>
            </Link>
            <Link
              underline="none"
              color="inherit"
              href="/business/lead-distribution-rules"
              aria-label="Lead Distribution Rules"
            >
              <StyledListItem
                sx={{ paddingLeft: "32px!important" }}
                selected={window.location.pathname.includes(
                  "lead-distribution-rules"
                )}
              >
                <ListItemText primary="Lead Distribution Rules" />
              </StyledListItem>
            </Link>
          </List>
        </Collapse>
         {planCategory && planCategory.includes("engage") && (
            <StyledListItem
              onClick={() => handleClick("whatsapp")}
              selected={window.location.pathname.includes("wa-")}
            >
              <ListItemIcon>
                <WhatsAppIcon />
              </ListItemIcon>
              <ListItemText primary="WhatsApp" />
              {open === "whatsapp" ||
              window.location.pathname.includes("wa-") ? (
                <ExpandLess />
              ) : (
                <ExpandMore />
              )}
            </StyledListItem>
          )}
        {planCategory && planCategory.includes("engage") && (
          <Collapse
            in={open === "whatsapp" || window.location.pathname.includes("wa-")}
            timeout="auto"
            className="submenu"
            unmountOnExit
          >
            <List component="div" disablePadding>
              <Link
                underline="none"
                color="inherit"
                href="/business/wa-business-settings"
                aria-label="Basic Settings"
              >
                <StyledListItem
                  sx={{ paddingLeft: "32px!important" }}
                  selected={window.location.pathname.includes(
                    "wa-business-settings"
                  )}
                >
                  <ListItemText primary="Basic Settings" />
                </StyledListItem>
              </Link>
              <Link
                underline="none"
                color="inherit"
                href="/business/wa-usage-report"
                aria-label="Usage and Reports"
              >
                <StyledListItem
                  sx={{ paddingLeft: "32px!important" }}
                  selected={window.location.pathname.includes(
                    "wa-usage-report"
                  )}
                >
                  <ListItemText
                    primary="Usage and Reports"
                    //
                  />
                </StyledListItem>
              </Link>
              <Link
                underline="none"
                color="inherit"
                href="/business/wa-template-messages"
                aria-label="Template Messages"
              >
                <StyledListItem
                  sx={{ paddingLeft: "32px!important" }}
                  selected={window.location.pathname.includes(
                    "wa-template-messages"
                  )}
                >
                  <ListItemText primary="Template Messages" />
                </StyledListItem>
              </Link>
              {/* <Link
              underline="none"
              color="inherit"
              href="/business/wa-integrations"
              aria-label="Integrations"
            >
              <StyledListItem
                sx={{ paddingLeft: "32px!important" }}
                selected={window.location.pathname.includes("wa-integrations")}
              >
                <ListItemText primary="Integrations" />
              </StyledListItem>
            </Link> */}
            </List>
          </Collapse>
        )}
        {planCategory && planCategory.includes("curate") && (
          <Link
            underline="none"
            color="inherit"
            href="/business/social-linking"
          >
            <StyledListItem
              selected={window.location.pathname.includes("social-linking")}
            >
              <ListItemIcon>
                <SocialIcon />
              </ListItemIcon>
              <ListItemText primary="Social Media Linking" />
            </StyledListItem>
          </Link>
        )}
      </List>
      <div className="bottom">
        {false && (
          <Link
            className="guide-text"
            underline="hover"
            onClick={() => exitSetting()}
          >
            {"Return to Evolve Next Workspace"}
          </Link>
        )}
        <Divider variant="fullWidth" style={{ color: "#24272c1a" }} />
        <div className="actions">
          <Link
            underline="none"
            color="inherit"
            className="payment"
            href="/all-payments/orders"
          >
            {"All Payments"}
          </Link>
          <Link
            underline="none"
            color="inherit"
            className="exit"
            onClick={() => exitSetting()}
          >
            {"Exit Settings"}
          </Link>
        </div>
      </div>
    </div>
  );
};

export default SideBar;
