import React, { Fragment, useState, useEffect } from "react";
import {
  Checkbox,
  Drawer,
  Divider,
  FormControlLabel,
  IconButton,
  Link,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  ListItemAvatar,
  ListItemIcon,
  Skeleton,
} from "@mui/material";
import { CircleOutlined, CheckCircleOutlined } from "@mui/icons-material";

// Loader imports
import CircularProgress from "@mui/material/CircularProgress";

import leftArrow from "../../../../assets/icons/leftArrow.svg";
import { UIButton } from "../../../../Components/exports";

// api imports
import {
  fetchLiUserPages,
  setupLinkedinBusiness,
} from "../../../../api/SocialMediaSetup";

// contexts
import { useToastMessage } from "../../../../contexts/ToastMessageContext";

// styles
import useStyles from "../styles/muiStyles";
const LinkedIn = ({
  anchor = "right",
  open,
  setPlatform,
  onClose,
  LIUser,
  setLIUser,
}) => {
  const classes = useStyles();
  const [selected, setSelected] = useState(null);

  const [showLoading, setShowLoading] = useState(false);
  const [pages, setPages] = useState([]);
  const [noUserOrgPagesFound, setNoUserOrgPagesFound] = useState(false);
  const [isOrgPagesError, setIsOrgPagesError] = useState(false);
  const [orgPagesError, setOrgPagesError] = useState(null);
  const [disableBtn, setDisableBtn] = useState(true);

  // contexts
  const { setErrorMessage } = useToastMessage();

  useEffect(() => {
    console.log("LIUser: ", LIUser);
  }, [LIUser]);

  // component effects
  useEffect(() => {
    const liToken = LIUser.token;

    // Add loader
    setShowLoading(true);

    fetchLiUserPages(liToken)
      .then((res) => {
        const { payload } = res;

        console.log("fetch li user pages data: ", payload);

        if (!payload) {
          return setErrorMessage("No organization data found in response!");
        }

        if (payload && !payload.length) {
          setShowLoading(false);
          setErrorMessage("No user linkedin organization pages found!");
          setNoUserOrgPagesFound(true);
          setDisableBtn(true);
        } else {
          // render the pages in the UI by setting state
          const liOrgData = payload.map((org) => {
            return {
              orgID: org.id,
              image: org.logo,
              name: org.name,
              selected: false,
              //inUse: org.inUse,
              inUse: false,
            };
          });
          setShowLoading(false);
          setDisableBtn(false);
          setPages(liOrgData);
        }
      })
      .catch((err) => {
        setErrorMessage(err.message);
        /*if (failPoint && failPoint.toLowerCase() === "fetchuserorgs") {
          setIsOrgPagesError(true);
          setOrgPagesError(err);
        } else {
          setErrorMessage(
            err || message || "Failed to fetch organization data!"
          );
        } */

        setShowLoading(false);
      });
  }, []);

  // Event handlers
  const handleLISetup = async () => {
    const liOrg = pages.find((page) => page.orgID === selected);

    if (!liOrg) {
      return setErrorMessage("Error finding organization!");
    }

    console.log("selected liOrg: ", liOrg);

    const { orgID, image: orgLogo, name: orgName } = liOrg;

    const { token, tokenSecret, id: userID } = LIUser;

    const liRequestData = {
      orgID,
      orgLogo,
      orgName,
      token,
      tokenSecret,
      linkedinUserId: userID,
    };

    try {
      const res = await setupLinkedinBusiness(liRequestData);
      if (res.success) {
        setPlatform(null);
        setLIUser(null);
      }
    } catch ({ error }) {
      setErrorMessage(error.message);
    }
  };

  const ListItemImage = ({ src, alt }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => {
      setLoading(false);
    };

    return (
      <ListItemAvatar>
        {loading && (
          <Skeleton
            variant="rectangular"
            className="avatar"
            width={50}
            height={50}
          />
        )}
        <img
          src={src}
          alt={alt}
          onLoad={onImageLoad}
          className={`avatar${loading ? " hidden" : ""}`}
          width={loading ? 0 : 50}
          height={loading ? 0 : 50}
        />
      </ListItemAvatar>
    );
  };

  return (
    <Drawer
      anchor={anchor}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes["social-linking"]}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            onClick={() => {
              onClose();
            }}
            style={{ color: "transparent", padding: 0 }}
          >
            <img alt="back" src={leftArrow} style={{ margin: "7.5px 10px" }} />
          </IconButton>
          <h1 className="title">{"Select Linkedin Page"}</h1>
        </div>
        <div className="content">
          {showLoading && <CircularProgress />}

          {isOrgPagesError && orgPagesError && orgPagesError.length && (
            <>
              <p>{orgPagesError}</p>
            </>
          )}

          {noUserOrgPagesFound && (
            <>
              <p>
                Sorry, we could not find any LinkedIn organization page
                associated with your account.
              </p>
            </>
          )}

          {pages.length > 0 && (
            <List>
              {pages.map((page) => (
                <div key={page.orgID}>
                  <ListItem sx={{ p: 0 }}>
                    <ListItemButton
                      onClick={() => setSelected(selected ? null : page.orgID)}
                    >
                      <ListItemImage
                        src={page.image}
                        alt={"LI organization profile image"}
                      />
                      <ListItemText
                        primary={page.name}
                        secondary={
                          page.inUse ? "Page already in use" : undefined
                        }
                      />
                      <ListItemIcon>
                        {selected === page.orgID ? (
                          <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                        ) : (
                          <CircleOutlined sx={{ color: "#F75D34" }} />
                        )}
                      </ListItemIcon>
                    </ListItemButton>
                  </ListItem>
                  <Divider />
                </div>
              ))}
            </List>
          )}
          <div className="actions">
            <UIButton
              variant="contained"
              className="btn-next"
              style={{
                marginTop: "40px",
                marginBottom: "30px",
                marginLeft: 0,
              }}
              disabled={disableBtn}
              onClick={handleLISetup}
            >
              {"Link LinkedIn Account"}
            </UIButton>
            {/* <Link
              className="skip"
              component="button"
              onClick={() => {
                setPlatform(null);
              }}
            >
              <span className="label">{"Skip Twitter"}</span>
              <svg
                width="6"
                height="10"
                viewBox="0 0 6 10"
                fill="none"
                xmlns="//www.w3.org/2000/svg"
              >
                <path
                  opacity="0.7"
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M0.483211 0.526985C0.221976 0.811968 0.241229 1.25477 0.526212 1.516L3.96329 4.66666L0.526212 7.81732C0.241229 8.07855 0.221976 8.52135 0.483211 8.80633C0.744446 9.09132 1.18724 9.11057 1.47223 8.84933L5.47223 5.18267C5.61687 5.05008 5.69922 4.86287 5.69922 4.66666C5.69922 4.47044 5.61687 4.28324 5.47223 4.15065L1.47223 0.483984C1.18724 0.222749 0.744446 0.242001 0.483211 0.526985Z"
                  fill="#24272C"
                />
              </svg>
            </Link> */}
          </div>
        </div>
      </div>
    </Drawer>
  );
};

export default LinkedIn;
