import React, { useState, useEffect } from "react";
import {
  Drawer,
  Divider,
  IconButton,
  Link,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  ListItemAvatar,
  ListItemIcon,
  Skeleton,
} from "@mui/material";
import { CircleOutlined, CheckCircleOutlined } from "@mui/icons-material";

import leftArrow from "../../../../assets/icons/leftArrow.svg";
import RightArrow from "../../../../assets/icons/rightArrow.svg";

import { UIButton } from "../../../../Components/exports";

// api imports
import {
  fetchBusinessFBPages,
  saveFBPageData,
  fetchIGAccountData,
  saveIGAccountData,
  getFbConnectedPageId,
} from "../../../../api/SocialMediaSetup";

// contexts imports
import { useToastMessage } from "../../../../contexts/ToastMessageContext";

// styles
import useStyles from "../styles/muiStyles";
import classes1 from "./NoInstagram.module.css";

const FaceBook = ({
  anchor = "right",
  open,
  setPlatform,
  onClose,
  onSuccess,
  fbUserId,
}) => {
  const classes = useStyles();
  const [
    stage, // ? 1 = facebook & 2 = instagram
    setStage,
  ] = useState(1);
  //const [selectedFbPageId, setSelectedFbPageId] = useState(null);

  const [alreadyConnected, setAlreadyConnected] = useState(false);

  const [selected, setSelected] = useState(null);
  const [noLinkedIGAcc, setNoLinkedIGAcc] = useState(false);
  const [BTNDisable, setBTNDisable] = useState(false);
  // FB states
  const [FBPages, setFBPages] = useState([]);
  const [noUserPagesFoundError, setNoUserPagesFoundError] = useState(false);

  // IG states
  const [IGAccount, setIGAccount] = useState(null);

  // contexts
  const { setErrorMessage } = useToastMessage();

  // component effects
  useEffect(() => {
    if (open) {
      getFbConnectedPageId().then(page => {
        if (page.success) {
          setSelected(page.payload);
          setAlreadyConnected(true);
        }
      });

      if (stage === 1) {
        fetchBusinessFBPages(fbUserId)
          .then(data => {
            const { payload: fb_pages } = data;
            if (!fb_pages.length) {
              return setNoUserPagesFoundError(true);
            }
            setFBPages(fb_pages);
          })
          .catch(error => {
            console.log("Fetch business fb pages error: ", error.message);
          });
      }

      if (stage === 2) {
        fetchIGAccountData()
          .then(data => {
            if (data.message === "not found") {
              setNoLinkedIGAcc(true);
              setBTNDisable(true);
              // setConnectIGURL(data.payload);
              //window.open(data.payload, "_blank");
            } else {
              const igAccData = {
                igAccID: data.payload.account_id,
                name: data.payload.name,
                username: data.payload.username,
                image: data.payload.profile_picture,
                // selected: false,
              };

              setIGAccount(igAccData);
            }
          })
          .catch(error => {
            console.log("error", error.message);
          });
      }
    }
  }, [open, stage]);

  const ListItemImage = ({ src, alt }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => {
      setLoading(false);
    };

    return (
      <ListItemAvatar>
        {loading && (
          <Skeleton
            variant='rectangular'
            className='avatar'
            width={50}
            height={50}
          />
        )}
        <img
          src={src}
          alt={alt}
          onLoad={onImageLoad}
          className={`avatar${loading ? " hidden" : ""}`}
          width={loading ? 0 : 50}
          height={loading ? 0 : 50}
        />
      </ListItemAvatar>
    );
  };

  // Event handlers
  const handleConfirmItemSelect = async () => {
    // fetch the page details from selected page id
    if (stage === 1) {
      // handle select fb page
      const selectedPage = FBPages.find(fbPage => fbPage.page_id === selected);

      // save the page data
      const page = {
        img_url: selectedPage.profile_image_url,
        page_id: selectedPage.page_id,
        page_name: selectedPage.page_name,
      };

      // setSelectedFbPageId(selectedPage.page_id);

      try {
        const { success } = await saveFBPageData(page);
        if (success) {
          setStage(2);
          setSelected(null);
        }
      } catch ({ error }) {
        setErrorMessage(error);
      }
    }

    if (stage === 2) {
      // handle select ig account

      if (!IGAccount) {
        return setErrorMessage("Please select an instagram account");
      }

      const igReqData = {
        account_id: IGAccount.igAccID,
        profile_picture: IGAccount.image,
        name: IGAccount.name,
        username: IGAccount.username,
      };

      try {
        const { success } = await saveIGAccountData(igReqData);
        if (success) {
          // close the drawer
          setPlatform(null);
          setStage(1);
          setSelected(null);
          onSuccess();
        }
      } catch (error) {
        const { message, error: err } = error;

        if (message) {
          setErrorMessage(message);
        } else if (err) {
          setErrorMessage(err);
        } else {
          setErrorMessage(error);
        }
      }
    }

    // stage === 1 && setStage(2);
    // stage === 1 && setSelected(null);
    // stage === 2 && setPlatform("twitter");
  };

  return (
    <Drawer
      anchor={anchor}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes["social-linking"]}
    >
      <div className='wrapper'>
        <div className='header'>
          <IconButton
            aria-label='Back'
            className='back-btn'
            onClick={() => {
              onClose();
              setStage(1);
            }}
            style={{ color: "transparent", padding: 0 }}
          >
            <img alt='back' src={leftArrow} style={{ margin: "7.5px 10px" }} />
          </IconButton>
          <h1 className='title'>
            {stage === 1 ? "Select Facebook Page" : "Select Instagram Account"}
          </h1>
        </div>
        <div className='content'>
          {!IGAccount && noLinkedIGAcc && stage === 2 && (
            <>
              <div className={classes1.Type0_Account}>
                <div className={classes1.Type0_Note}></div>
                <main className={classes1.Type0_Link}>
                  <p>Instagram Account Not Connected</p>
                  <button
                    className={classes1.Type0_Button}
                    onClick={() =>
                      window.open(
                        `https://www.facebook.com/settings?tab=linked_instagram`,
                        "_blank"
                      )
                    }
                  >
                    Link Manually
                  </button>
                  <p className={classes1.instruction}>
                    Click the button once you are logged into your associated
                    Facebook Page on another tab.
                  </p>

                  <p
                    className={classes1.skipBtn}
                    onClick={() => {
                      onClose();
                      setStage(1);
                    }}
                  >
                    Continue later
                    <img src={RightArrow} alt='skip' />
                  </p>
                </main>
              </div>
            </>
          )}

          {stage === 1 ? (
            <>
              {noUserPagesFoundError && (
                <>
                  <p>
                    Sorry, we couldn't find any Facebook page associated with
                    your account.
                  </p>
                </>
              )}
              {FBPages.length > 0 && (
                <List>
                  {FBPages.map(page => (
                    <div key={page.page_id}>
                      <ListItem sx={{ p: 0 }}>
                        <ListItemButton
                          onClick={() =>
                            !alreadyConnected
                              ? setSelected(selected ? null : page.page_id)
                              : undefined
                          }
                        >
                          {/* <ListItemImage src={"image"} alt={"name"} /> */}
                          <ListItemImage
                            src={page.profile_image_url}
                            alt={"FB page image url"}
                          />
                          <ListItemText
                            primary={page.page_name}
                            secondary={
                              page.isUsedByBusiness
                                ? "Page already in use"
                                : undefined
                            }
                          />
                          <ListItemIcon>
                            {selected === page.page_id ? (
                              <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                            ) : (
                              <CircleOutlined
                                sx={{
                                  color: alreadyConnected
                                    ? "#e2d8d5"
                                    : "#F75D34",
                                }}
                              />
                            )}
                          </ListItemIcon>
                        </ListItemButton>
                      </ListItem>

                      <Divider />
                    </div>
                  ))}
                </List>
              )}
            </>
          ) : (
            <List>
              {IGAccount && (
                <>
                  <ListItem sx={{ p: 0 }}>
                    <ListItemButton
                      onClick={() =>
                        setSelected(selected ? null : IGAccount.igAccID)
                      }
                    >
                      <ListItemImage src={IGAccount.image} alt={"name"} />
                      <ListItemText primary={IGAccount.name} />
                      <ListItemIcon>
                        {selected === IGAccount.igAccID ? (
                          <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                        ) : (
                          <CircleOutlined sx={{ color: "#24272C80" }} />
                        )}
                      </ListItemIcon>
                    </ListItemButton>
                  </ListItem>

                  <Divider />
                </>
              )}
            </List>
          )}

          {stage === 1 && (
            <div className='actions'>
              <UIButton
                variant='contained'
                className='btn-next'
                style={{
                  marginTop: "40px",
                  marginBottom: "30px",
                  marginLeft: 0,
                }}
                disabled={selected === null}
                onClick={handleConfirmItemSelect}
              >
                Continue to Instagram
              </UIButton>
            </div>
          )}

          {IGAccount && stage === 2 && (
            <div className='actions'>
              <UIButton
                variant='contained'
                className='btn-next'
                style={{
                  marginTop: "40px",
                  marginBottom: "30px",
                  marginLeft: 0,
                }}
                disabled={selected === null}
                onClick={handleConfirmItemSelect}
              >
                Link Instagram Account
              </UIButton>
            </div>
          )}
        </div>
      </div>
    </Drawer>
  );
};

export default FaceBook;
