import React, { useState } from "react";
import {
  Drawer,
  IconButton,
  Link,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  ListItemAvatar,
  ListItemIcon,
  Skeleton,
} from "@mui/material";
import { CircleOutlined, CheckCircleOutlined } from "@mui/icons-material";

import leftArrow from "../../../../assets/icons/leftArrow.svg";
import { UIButton } from "../../../../Components/exports";

// api imports
import { setupTwitterBusiness } from "../../../../api/SocialMediaSetup";

// styles
import useStyles from "../styles/muiStyles";
const Twitter = ({
  anchor = "right",
  open,
  setPlatform,
  onClose,
  TWUser,
  setTWUser,
}) => {
  const classes = useStyles();
  const [selected, setSelected] = useState(null);

  const ListItemImage = ({ src, alt }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => {
      setLoading(false);
    };

    return (
      <ListItemAvatar>
        {loading && (
          <Skeleton
            variant="rectangular"
            className="avatar"
            width={50}
            height={50}
          />
        )}
        <img
          src={src}
          alt={alt}
          onLoad={onImageLoad}
          className={`avatar${loading ? " hidden" : ""}`}
          width={loading ? 0 : 50}
          height={loading ? 0 : 50}
        />
      </ListItemAvatar>
    );
  };

  // Event handlers
  const updateTWAccount = async () => {
    try {
      const { success } = await setupTwitterBusiness(TWUser);

      if (success) {
        setPlatform(null);
        setTWUser(null);
      }
    } catch ({ error }) {
      console.log(error);
    }
  };

  return (
    <Drawer
      anchor={anchor}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") && onClose()
      }
      className={classes["social-linking"]}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            onClick={() => {
              onClose();
            }}
            style={{ color: "transparent", padding: 0 }}
          >
            <img alt="back" src={leftArrow} style={{ margin: "7.5px 10px" }} />
          </IconButton>
          <h1 className="title">{"Twitter Account is Linked"}</h1>
        </div>
        <div className="content">
          <List>
            {TWUser && (
              <ListItem sx={{ p: 0 }}>
                <ListItemButton
                  onClick={() => setSelected(selected ? null : TWUser.twUserID)}
                >
                  <ListItemImage
                    src={TWUser.profileImgURL}
                    alt={"Twitter profile image"}
                  />
                  <ListItemText
                    primary={TWUser.twScreenName}
                    secondary={false ? "Page already in use" : undefined}
                  />
                  <ListItemIcon>
                    {selected === TWUser.twUserID ? (
                      <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                    ) : (
                      <CircleOutlined sx={{ color: "#F75D34" }} />
                    )}
                  </ListItemIcon>
                </ListItemButton>
              </ListItem>
            )}
            <ListItemText
              className="another-connection"
              primary={
                <Link component="button" underline="hover">
                  {"Or Connect another twitter account"}
                </Link>
              }
            />
          </List>
          <div className="actions">
            <UIButton
              variant="contained"
              className="btn-next"
              style={{
                marginTop: "40px",
                marginBottom: "30px",
                marginLeft: 0,
              }}
              // onClick={() => {
              //   setPlatform("linkedin");
              // }}
              onClick={updateTWAccount}
            >
              {"Link Twitter Account"}
            </UIButton>
            {/* <Link
              className="skip"
              component="button"
              onClick={() => {
                setPlatform("linkedin");
              }}
            >
              <span className="label">{"Skip Twitter"}</span>
              <svg
                width="6"
                height="10"
                viewBox="0 0 6 10"
                fill="none"
                xmlns="//www.w3.org/2000/svg"
              >
                <path
                  opacity="0.7"
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M0.483211 0.526985C0.221976 0.811968 0.241229 1.25477 0.526212 1.516L3.96329 4.66666L0.526212 7.81732C0.241229 8.07855 0.221976 8.52135 0.483211 8.80633C0.744446 9.09132 1.18724 9.11057 1.47223 8.84933L5.47223 5.18267C5.61687 5.05008 5.69922 4.86287 5.69922 4.66666C5.69922 4.47044 5.61687 4.28324 5.47223 4.15065L1.47223 0.483984C1.18724 0.222749 0.744446 0.242001 0.483211 0.526985Z"
                  fill="#24272C"
                />
              </svg>
            </Link> */}
          </div>
        </div>
      </div>
    </Drawer>
  );
};

export default Twitter;
